<script setup>
import { mdiCrosshairs, mdiCrosshairsGps } from '@mdi/js';
import Icon from '@/components/Svg.vue';
const props = defineProps({
    data: {
        type: Object,
        required: true
    },
});
const grid= (ans)=> {
      var overFlow = false;
      ans.forEach(e => {
        if(e.length>10){
          overFlow=true;
        }
      });
      return overFlow?'grid-cols-1':'grid-cols-2';
    }
const style = (styles)=> {
      var style = '';
      for (const [key,value] of Object.entries(styles)) {
        style+=value + ' ';
      }
      return style;
}
const titleLead = (title)=> {
        var myArray = title.split('｜');
        var html='';
        html+=`<div class="flex flex-row text-md flex-wrap">`;
        myArray.forEach(ans => {
            if(ans.includes('！')){
                let star = ans.substring(1);             
                html+=`<div class="underline">&nbsp&nbsp&nbsp${star}&nbsp&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }else{
                let tmpArray = ans.split('');
                tmpArray.forEach(ele => {
                   html+=`<div>${ele}</div>`; 
                });
            }
        });
        html+=`</div>`;
        return  html;
}
const paragraphs = (text)=> {
        var myArray = text.split('｜');
        var html='';
        html+=`
          <div class="flex flex-row text-x11 flex-wrap">
          <div >&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp</div>
        `;
        myArray.forEach(ans => {
            if(ans.includes('！')){
                let star = ans.substring(1);             
                html+=`<div class="underline font-bold">&nbsp&nbsp&nbsp${star}&nbsp&nbsp&nbsp</div>`;
                html+=`<div class="">&nbsp</div>`;
            }else{
                let tmpArray = ans.split('');
                tmpArray.forEach(ele => {
                   html+=`<div>${ele}</div>`; 
                });
            }
        });
        html+=`</div>`;
        return  html;
    }
</script>
<template>
  <div class="pt-5"></div>
  <!-- mondai title -->
  <div 
    v-html="titleLead(data.title)" 
    class="font-bold px-3 pt-2 pb-4 lg:pb-8 text-sm select-none"
  ></div>
  <!-- Boxs -->
 <div 
    v-for="(box,index) in data.boxs"
    v-bind:key="index"
  > 
    <!-- explain -->
    <div class="px-3 py-4 text-sm text-left select-none"> {{box.explain}} </div> 
    <!-- box -->
    <div class="mx-5 my-5 py-4 lg:py-8 px-2 lg:px-8 border-2 border-gray-500 select-none">
      <!-- title -->
      <div 
        v-for="(title,index) in box.title.contents"
        v-bind:key="index"
        v-bind:class="style(box.title.style)"
      >
        <div>{{title}}</div>
      </div>
      <!-- subTitle -->
     <div 
        v-for="(subTitle,index) in box.subTitle.contents"
        v-bind:key="index"
        v-bind:class="style(box.subTitle.style)"
      >
        <div>{{subTitle}}</div>
      </div>
      <!-- paragraphs -->
      <div 
        v-for="(paragraph,index) in box.paragraphs"
        v-bind:key="index"
      >
        <div v-html="paragraphs(paragraph)" class="py-1 leading-loose"></div>
      </div>
      <!-- tail -->
      <div 
        v-for="(tail,index) in box.tail.contents"
        v-bind:key="index"
        v-bind:class="style(box.tail.style)"
      >
        <div>{{tail}}</div>
      </div>
      <!-- answer -->
     <div class="my-3"></div>
     <div
        v-for="(answerBox,index) in data.answerBoxs"
        v-bind:key="index"
      > 
    <!-- number -->
      <div class="mx-3 pt-2 pb-4 select-none">({{answerBox.number}})</div> 
    <!-- button -->
      <div v-bind:class="grid(answerBox.answers)" class="px-5 pb-4 grid gap-2 md:gap-6 select-none">
      <div 
        v-for="(answer,i) in answerBox.answers"
        v-bind:key="i"
      >
        <button
          v-if="answerBox.pick!=answer"
          v-on:click="data.answerBoxs[index].pick=answer"
          class="flex flex-row w-full h-12 px-3 bg-gray-200 items-center justify-start rounded-md">
          <Icon :path="mdiCrosshairs" size="20" class="text-gray-500"/>
          <div class="pl-2 text-xs text-left">{{answer}}</div>
        </button>
        <button 
            v-if="answerBox.pick==answer"
            class="flex flex-row w-full h-12 px-3 font-bold bg-gray-300 items-center justify-start rounded-md">
            <Icon :path="mdiCrosshairsGps" size="20" class="text-blue-500 font-bold"/>
            <div class="pl-2 text-xs　text-left">{{answer}}</div>
        </button>
      </div>
    </div>
    </div>
  </div>  
      
    <!-- >>>>>>>>>>> -->
      <!-- Revise in Apr 19 -->
    <!-- <<<<<<<<<<< -->
  <!-- </div> -->

  
  </div>

</template>

